.how-container {
    background-color: white;
    position: relative;
}
.how-container h2 {
    font-size: 2.2em;
    text-align: center;
    margin-bottom: 2em;
}
.how-categories {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    max-width: 1200px;
    margin: 0 auto;
    position: relative;
}
.hero-wave {
    position: relative;
    bottom: 150px;
}
.pos-categories {
    position: relative;
    bottom: 250px;
}
.how-image {
    height: 130px;
    width: 220px;
    margin: 0 auto;
}
.how-item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
}
.how-item h4 {
    text-align: center;
    font-size: 1.4em;
    padding-top: .5em;
}
.how-item p {
    text-align: center;
    padding-top: 1em;
    max-width: 300px;
}
#image-management {
    position: relative;
    right: 10px;
}
#image-tactics {
    position: relative;
    right: 20px;
}